<template>
    <v-container>
        <v-data-table
            :headers="headers"
            :items="items"
            sort-by="id"
            class="elevation-1"
        >
            <template v-slot:top>
                <v-toolbar flat>
                    <v-toolbar-title>Lista de Trabajadores</v-toolbar-title>
                    <v-divider class="mx-4" inset vertical></v-divider>
                    <v-spacer></v-spacer>
                    <v-dialog v-model="dialog" max-width="500px">
                        <template v-slot:activator="{ on, attrs }">
                            <v-btn
                                color="primary"
                                dark
                                class="mb-2"
                                v-bind="attrs"
                                v-on="on"
                            >
                                Nuevo
                            </v-btn>
                        </template>
                        <v-card>
                            <v-card-title>
                                <span class="text-h5">{{ formTitle }}</span>
                            </v-card-title>

                            <v-alert
                                v-if="show_mensaje"
                                class="rounded-0 my-2"
                                dense
                                text
                                dismissible
                                :type="type_mensaje"
                            >
                                <small> {{ mensaje }} </small>
                            </v-alert>

                            <v-card-text>
                                <v-container>
                                    <v-form
                                        ref="form"
                                        v-model="valid"
                                        lazy-validation
                                    >
                                        <input
                                            type="text"
                                            hidden
                                            v-model="editedItem.id"
                                        />
                                        <v-row>
                                            <v-col
                                                class="py-1"
                                                cols="12"
                                                sm="6"
                                            >
                                                <v-text-field
                                                    v-model="editedItem.nombre"
                                                    label="Nombre(s)"
                                                    :rules="[rules.required]"
                                                ></v-text-field>
                                            </v-col>
                                            <v-col
                                                class="py-1"
                                                cols="12"
                                                sm="6"
                                            >
                                                <v-text-field
                                                    v-model="
                                                        editedItem.apellidos
                                                    "
                                                    :rules="[rules.required]"
                                                    label="Apellidos"
                                                ></v-text-field>
                                            </v-col>
                                            <v-col
                                                class="py-1"
                                                cols="12"
                                                sm="6"
                                            >
                                                <v-text-field
                                                    v-model="
                                                        editedItem.documento
                                                    "
                                                    label="Num. Doc."
                                                    :rules="[rules.required]"
                                                ></v-text-field>
                                            </v-col>
                                            <v-col
                                                class="py-1"
                                                cols="12"
                                                sm="6"
                                            >
                                                <v-text-field
                                                    v-model="editedItem.correo"
                                                    label="Correo"
                                                    :rules="[rules.required]"
                                                ></v-text-field>
                                            </v-col>
                                            <v-col
                                                class="py-1"
                                                cols="12"
                                                sm="6"
                                            >
                                                <v-text-field
                                                    v-model="
                                                        editedItem.nacionalidad
                                                    "
                                                    label="Nacionalidad"
                                                    :rules="[rules.required]"
                                                ></v-text-field>
                                            </v-col>
                                            <v-col
                                                class="py-1"
                                                cols="12"
                                                sm="6"
                                            >
                                                <v-text-field
                                                    :rules="[rules.required]"
                                                    v-model="editedItem.celular"
                                                    label="Celular"
                                                ></v-text-field>
                                            </v-col>

                                            <v-col class="py-1" cols="6">
                                                <v-select
                                                    :rules="[rules.required]"
                                                    class="mt-0 pt-0"
                                                    v-model="editedItem.oficina"
                                                    :items="oficinas"
                                                    single-line
                                                    item-value="id"
                                                    item-text="text"
                                                    placeholder="Selecionar una oficina"
                                                />
                                            </v-col>

                                            <v-col class="py-1" cols="6">
                                                <v-select
                                                    :rules="[rules.required]"
                                                    class="mt-0 pt-0"
                                                    v-model="editedItem.puesto"
                                                    :items="puestos"
                                                    single-line
                                                    item-value="id"
                                                    item-text="text"
                                                    placeholder="Selecionar un puesto"
                                                />
                                            </v-col>

                                            <v-col class="py-1" cols="12">
                                                <v-select
                                                    :rules="[rules.required]"
                                                    class="mt-0 pt-0"
                                                    item-value="id"
                                                    item-text="text"
                                                    v-model="editedItem.horario"
                                                    :items="horarios"
                                                    single-line
                                                    placeholder="Selecionar un hoarario"
                                                />
                                            </v-col>

                                            <v-col cols="12" class="py-1">
                                                <v-spacer></v-spacer>

                                                <v-btn
                                                    color="red darken-1"
                                                    text
                                                    @click="close"
                                                >
                                                    cancelar
                                                </v-btn>
                                                <v-btn
                                                    :disabled="!valid"
                                                    color="primary darken-1"
                                                    :loading="loading_btn"
                                                    @click="Guardar"
                                                >
                                                    guardar
                                                </v-btn>
                                            </v-col>
                                        </v-row>
                                    </v-form>
                                </v-container>
                            </v-card-text>
                        </v-card>
                    </v-dialog>
                    <v-dialog v-model="dialogDelete" max-width="300px">
                        <v-card dark>
                            <h5 class="text-center text--white pa-5">
                                Â¿Seguro de eliminar el registro?
                            </h5>
                            <v-divider></v-divider>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn
                                    color="grey darken-1"
                                    text
                                    @click="closeDelete"
                                    >Cancel</v-btn
                                >
                                <v-btn
                                    color="red darken-1"
                                    text
                                    @click="deleteItemConfirm"
                                    >OK</v-btn
                                >
                                <v-spacer></v-spacer>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-toolbar>
            </template>
            <template v-slot:item.actions="{ item }">
                <v-icon small class="mr-2" @click="editItem(item)">
                    mdi-pencil
                </v-icon>
                <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
            </template>
            <template v-slot:no-data>
                <v-btn color="primary" @click="initialize"> Actualizar </v-btn>
            </template>
        </v-data-table>
    </v-container>
</template>

<script>
import Layout from "@/Layouts/AdminLayout";
export default {
    metaInfo: { title: "Trabajadores" },
    layout: Layout,
    props: {
        trabajadores: Array,
        horarios: Array,
        oficinas: Array,
        puestos: Array,
    },
    data: () => ({
        loading_btn: false,
        show_mensaje: false,
        type_mensaje: "success",
        mensaje: "",

        valid: true,
        dialog: false,
        dialogDelete: false,
        headers: [
            {
                text: "ID",
                align: "start",
                sortable: false,
                value: "id",
            },
            { text: "Nombre", value: "nombre" },
            { text: "Apellidos", value: "apellidos" },
            { text: "Celular", value: "celular" },
            { text: "Horario", value: "horas.horario" },
            { text: "DNI", value: "documento" },
            { text: "Actions", value: "actions", sortable: false },
        ],
        items: [],
        editedIndex: -1,
        editedItem: {
            id: false,
            nombre: "",
            apellidos: "",
            correo: "",
            nacionalidad: "",
            celular: "",
            documento: "",
            puesto: "",
            oficina: "",
            horario: "",
            horas: "",
            //codigo: "",
            //estado: "",
        },
        defaultItem: {
            id: false,
            nombre: "",
            apellidos: "",
            correo: "",
            nacionalidad: "",
            celular: "",
            documento: "",
            puesto: "",
            oficina: "",
            horario: "",
            horas: "",
            //codigo: "",
            //estado: "",
        },
        rules: {
            required: (value) => !!value || "Requerido.",
            min: (v) => v.length >= 8 || "Min 6 caracteres",
            email: (v) => /.+@.+\..+/.test(v) || "Formato incorrecto",
        },
    }),

    computed: {
        formTitle() {
            return this.editedIndex === -1
                ? "Nuevo Registro"
                : "Editar Registro"; //editedItem
        },
    },

    watch: {
        dialog(val) {
            val || this.close();
        },
        dialogDelete(val) {
            val || this.closeDelete();
        },
    },

    created() {
        this.initialize();
    },

    methods: {
        initialize() {
            this.items = this.trabajadores;
        },

        editItem(item) {
            this.editedIndex = this.items.indexOf(item);
            this.editedItem = Object.assign({}, item);
            this.dialog = true;
        },

        deleteItem(item) {
            this.editedIndex = this.items.indexOf(item);
            this.editedItem = Object.assign({}, item);
            this.dialogDelete = true;
        },

        async deleteItemConfirm() {
            let res = await axios.delete(
                "/trabajador/eliminar/" + this.editedItem.id
            );

            if (res.data.estado) {
                this.items.splice(this.editedIndex, 1);
            }

            this.closeDelete();
        },

        close() {
            this.dialog = false;
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1;
            });
        },

        closeDelete() {
            this.dialogDelete = false;
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1;
            });
        },

        async Guardar() {
            if (this.$refs.form.validate()) {
                try {
                    this.loading_btn = true;

                    let res = await axios.post(
                        "/trabajador/guardar",
                        this.editedItem
                    );

                    if (this.editedIndex > -1) {
                        Object.assign(
                            this.items[this.editedIndex],
                            res.data.datos
                        );
                    } else {
                        this.items.push(res.data.datos);
                    }

                    //this.show_mensaje = true;
                    //this.mensaje = res.data.mensaje;
                    this.loading_btn = false;

                    this.editedItem = this.defaultItem;
                } catch (error) {
                    this.type_mensaje = "error";
                    this.mensaje = error.response.data.error;
                    this.show_mensaje = true;
                    this.loading_btn = false;
                    this.reset_formulario();
                } finally {
                    this.close();
                }
            } else {
                console.log("ere");
            }
        },
    },
};
</script>
